@import "_variables";
@import "fonts";
@import "reset";
@import url('https://fonts.googleapis.com/css2?family=B612+Mono:ital,wght@0,400;0,700;1,400&family=DM+Serif+Display&display=swap');

html {
    scroll-behavior: smooth;
}

img {
  max-width: 100%;
  width: 100%;
}

body {
  font-family: 'DM Serif Display', serif;
  background-color: #fff;
  padding: 45px; 
}

header {
  background-color: #000;
  border: 1px solid #000;
  margin-bottom: 80px;

  nav {
    ul {
      display: grid;
      gap: 1px;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(2, 60px);

      li {
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;

        &:nth-child(2) {
          grid-column-start: span 2;
          font-size: 32px;
          text-transform: uppercase;
          font-family: 'DM Serif Display', serif;
        }
      }
    }
  }
}

main {
  h1, h6 {
    text-align: center;
  }
  h1 {
    font-size: 58px;
    margin-bottom: 20px;
  }
  h6 {
    font-family: 'B612 Mono', monospace;
    font-size: 12px;
    margin-bottom: 50px;
  }
}
